



























































import allApi from '../../common/allApi.js'
import http from '../../common/http.js'
let tools = require('../../common/tools.js');
import {  
    mapState
} from 'vuex';

export default {
	data() {
		return {
			popData: [
				
			],
			userInfo: {
				
			},
			role: true, // 是否有权限获取用户信息
			
		}
		
	},
	onPullDownRefresh(){
		this.getData()
	},
	async onLoad() {
		let yes = await tools.isLogin()
		if(!yes){ // 验证失败
			let register = await tools.logining()
			if(!register){ // 需要注册
				// 设置登录or注册完成后的返回地址
				let pages = getCurrentPages();
				let path = (pages[pages.length - 1].route).slice((pages[pages.length - 1].route).lastIndexOf('/') + 1)
				uni.redirectTo({
					url: '../userInfoAuth/userInfoAuth?goBack=' + path 
				})
			}
		}
		this.getData()
		let _th = this
		uni.getUserInfo({
			success(res) {
				_th.userInfo = res.userInfo
			},
			fail(err) {
				// 没有授权
			}
		})
		
		// 获取 MoneyHide
		this.$store.commit('setMoneyHide', await tools.getMoneyHide())
		
	},
	async onShow() {
		let _this = this
		uni.getSetting({
			success(res) {
				if(res.authSetting['scope.userInfo']){
					_this.role = true
				}else{
					_this.role = false
				}
			},
			fail() {
				_this.role = false
			}
		})
	},
	
	methods: {
		goTel(){
			uni.navigateTo({
				url: '../tel/tel'
			})
		},
		getData(){
			let par = {
				pageNumber: 1,
				pageSize: 10000
			};
			http.rq({
				url: allApi.unlockScenic,
				data: par,
				loading: true
			}).then(res => {
				uni.stopPullDownRefresh()
				if(res.code === 200){
					this.popData = res.data
				}else{
					this.popData = []
				}
			})
			
		},
		getScDetail(item){
			uni.navigateTo({
				url:'../scMapDetail/scMapDetail?id=' + item.id
			})
		},
		onGotUserInfo(e){
			if(e.detail.userInfo){
				this.userInfo = e.detail.userInfo
				this.role = true
			}else{
				this.role = false
			}
		}
	},
	watch: {
		
	},
	computed: {
		...mapState(['MoneyHide'])  
	}
}
